'use client'

import { useEffect, useState } from 'react'
import { Card, CardContent } from '@/components/ui/card'
import {
  Leaf,
  TrendingUp,
  Layers,
  CheckCircle2,
} from 'lucide-react'

type DashboardStats = {
  assigned_farmers: number
  total_fields: number
  total_crops: number
  active_crops: number
}

export function QuickStats() {
  const [stats, setStats] = useState<DashboardStats | null>(null)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const fetchStats = async () => {
      try {
        const token = localStorage.getItem('authToken')
        if (!token) return

        const res = await fetch(
          'https://rda.ngrok.app/api/dashboard/stats/',
          {
            headers: {
              Authorization: `Bearer ${token}`,
              'ngrok-skip-browser-warning': 'true',
            },
          }
        )

        if (!res.ok) {
          throw new Error(`API Error: ${res.status}`)
        }

        const data = await res.json()
        console.log('✅ Dashboard Stats:', data)

        setStats(data)
      } catch (err) {
        console.error('❌ Failed to load dashboard stats:', err)
      } finally {
        setLoading(false)
      }
    }

    fetchStats()
  }, [])

  const cards = [
    {
      label: 'Assigned Farmers',
      value: stats?.assigned_farmers,
      icon: Leaf,
      bg: 'bg-primary/10',
      color: 'text-primary',
    },
    {
      label: 'Total Fields',
      value: stats?.total_fields,
      icon: Layers,
      bg: 'bg-secondary/10',
      color: 'text-secondary',
    },
    {
      label: 'Total Crops',
      value: stats?.total_crops,
      icon: TrendingUp,
      bg: 'bg-accent/10',
      color: 'text-accent',
    },
    {
      label: 'Active Crops',
      value: stats?.active_crops,
      icon: CheckCircle2,
      bg: 'bg-green-500/10',
      color: 'text-green-600',
    },
  ]

  return (
    <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-4">
      {cards.map((card) => (
        <Card key={card.label}>
          <CardContent className="p-6 flex justify-between items-center">
            <div>
              <p className="text-sm text-muted-foreground">
                {card.label}
              </p>
              <p className="text-2xl font-bold mt-1">
                {loading ? '...' : card.value}
              </p>
            </div>

            <div className={`${card.bg} p-3 rounded-lg`}>
              <card.icon className={`${card.color} w-6 h-6`} />
            </div>
          </CardContent>
        </Card>
      ))}
    </div>
  )
}
